
.registration-form{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin:0.6em
}
.registration-form .container{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row
}
.registration-form .terms-of-service{-ms-flex:0 1 50%;flex:0 1 50%;margin:0.8em
}
.registration-form .text-fields{margin-top:0.6em;-ms-flex:1 0;flex:1 0;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.registration-form .form-group{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;padding:0.3em 0.0em 0.3em;line-height:24px;margin-bottom:1em
}
@keyframes shakeError{
0%{transform:translateX(0)
}
15%{transform:translateX(0.375rem)
}
30%{transform:translateX(-0.375rem)
}
45%{transform:translateX(0.375rem)
}
60%{transform:translateX(-0.375rem)
}
75%{transform:translateX(0.375rem)
}
90%{transform:translateX(-0.375rem)
}
100%{transform:translateX(0)
}
}
.registration-form .form-group--error{animation-name:shakeError;animation-duration:.6s;animation-timing-function:ease-in-out
}
.registration-form .form-group--error .form--label{color:#f04124;color:var(--cRed, #f04124)
}
.registration-form .form-error{margin-top:-0.7em;text-align:left
}
.registration-form .form-error span{font-size:12px
}
.registration-form .form-error ul{list-style:none;padding:0 0 0 5px;margin-top:0
}
.registration-form .form-error ul li::before{content:" "
}
.registration-form form textarea{line-height:16px;resize:vertical
}
.registration-form .captcha{max-width:350px;margin-bottom:0.4em
}
.registration-form .btn{margin-top:0.6em;height:28px
}
.registration-form .error{text-align:center
}
@media all and (max-width: 959px){
.registration-form .container{-ms-flex-direction:column-reverse;flex-direction:column-reverse
}
}



