
.setting-item{border-bottom:2px solid var(--fg, #182230);margin:1em 1em 1.4em;padding-bottom:1.4em
}
.setting-item>div{margin-bottom:.5em
}
.setting-item>div:last-child{margin-bottom:0
}
.setting-item:last-child{border-bottom:none;padding-bottom:0;margin-bottom:1em
}
.setting-item select{min-width:10em
}
.setting-item textarea{width:100%;height:100px
}
.setting-item .unavailable,.setting-item .unavailable i{color:var(--cRed, red);color:red
}
.setting-item .old-avatar{width:128px;border-radius:4px;border-radius:var(--avatarRadius, 4px)
}
.setting-item .new-avatar{object-fit:cover;width:128px;height:128px;border-radius:4px;border-radius:var(--avatarRadius, 4px)
}
.setting-item .btn{min-height:28px;min-width:10em;padding:0 2em
}
.select-multiple{display:-ms-flexbox;display:flex
}
.select-multiple .option-list{margin:0;padding-left:.5em
}
.setting-list,.option-list{list-style-type:none;padding-left:2em
}
.setting-list li,.option-list li{margin-bottom:0.5em
}
.setting-list .suboptions,.option-list .suboptions{margin-top:0.3em
}



